@import
'../../../../styles/utils/variables',
'../../../../styles/utils/mixins';

.containerProgressBarLines {
  display: flex;
  flex-direction: column;

  margin-top: 20px;
  margin-left: 15px;
  width: 84%;

  @include respond-to('medium') {
    //margin-top: 25px;
  }
}

.containerProgressIcon {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.containerProgressTitle {
  display: flex;
  flex-direction: column;
}

.progressBarTitle {
  color: $light_grey;
  font-family: $roboto;
  font-size: 14px;

  @include respond-to('small') {
    font-size: 11px;
  }
}

.progressBarCost {
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;

  color: $white;
  font-family: $roboto;
}

.iconDropdown {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  margin-right: 0;
  margin-top: 10px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-top: 10px;
  }
}

.progressLine {
  position: absolute;
  margin-top: 50px;

  height: 1%;
  width: 39%;

  @include respond-to('medium') {
    height: 1.3%;
  }

  @include respond-to('small') {
    width: 39%;
  }
}