@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container{
  position: relative;
    width: calc(100% - 900px);
    position: fixed;
    right: 0;
    bottom: 80px;
    height: 167px;
    font-family: "Roboto", Arial, sans-serif;
    padding: 0 30px;
    box-sizing: border-box;
    color: #fff;
    display: flex;
    justify-content: stretch;
    &:before{
      content: '';
      position: absolute;
      left: 30px;
      top: 0;
      width: 1px;
      height: 134px;
      background: linear-gradient(180deg, rgba(199, 213, 236, 0) 0%, rgba(109, 127, 158, 0) 0.01%, #6D7F9E 25%);
      z-index: 2;
      @include respond-to('medium') {
        left: 16px;
      }
    }
    @include respond-to('medium') {
      width: calc(100% - 800px);
      padding: 0 16px;
    }
  
    @include respond-to('small') {
      width: calc(100% - 656px);
    }
}

.periods{
  &__line{
    position: absolute;
    width: calc(100% - 60px);
    height: 1px;
    border-top: 1px dashed #8B9CB8;
    box-sizing: border-box;
    opacity: 0.5;
    &__4{
      bottom: 32px;
    }
    &__3{
      bottom: 65px;
    }
    &__2{
      bottom: 98px;
    }
    &__1{
      bottom: 131px;
    }
  }
}