@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.containerMenu {
  position: fixed;
  z-index: 3;
  font-weight: 200;
  height: 100vh;
}

.menu {
  position: relative;
  top: 0;
  left: 0;
  width: 120px;

  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;

  background: $darkest_grey;

  height: 100%;
  padding: 0;

  @include respond-to('small') {
    width: 100px;
  }
}

.logoIconContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 95%;
  margin-top: 10px;
}

.logoIcon {
    width: 48px;
    height: 50px;

    cursor: pointer;
}

.iconsContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  margin-top: 100px;

  @include respond-to('medium') {
    margin-top: 60px;
  }

  @include respond-to('small') {
    margin-top: 40px;
  }
}

.icons {
  width: 29px;
  height: 29px;

  cursor: pointer;
}

.menuItem {
  display: flex;
  align-items: center;
  flex-direction: column;
  cursor: pointer;

  width: 90%;
  height: 100%;

  padding: 20px 0;

  margin-top: 20px;
  border-radius: 4px;

  &:hover {
    background-color: $grey;
  }

  @include respond-to('medium') {
    padding: 10px 0;
  }

  @include respond-to('small') {
    margin-top: 15px;
  }
}

.activeMenu {
  background-color: $grey;
}

.labelUpper {
  color: $white;
  text-transform: uppercase;
  margin-top: 15px;
  font-size: 12px;
  font-family: $roboto;
  text-align: center;

  @include respond-to('small') {
    font-size: 10px;
  }
}

.labelLower {
  color: $white;
  margin-top: 15px;
  font-size: 12px;
  font-family: $roboto;
  text-align: center;

  @include respond-to('small') {
    font-size: 10px;
  }
}

.themeContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
  position: absolute;
  bottom: 80px;

  @include respond-to('small') {
    position: static;
    margin-top: 40px;
  }
}

.iconsTheme {
  width: 25px;
  height: 25px;

  margin-top: 10px;

  cursor: pointer;

  @include respond-to('small') {
    width: 20px;
    height: 20px;
  }
}