@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  position: absolute;
  left: 130px;

  max-height: calc(100vh - 70px);

  overflow-y: scroll;
  overflow-x: hidden;

  @include respond-to('small') {
    left: 110px;
    height: calc(100vh - 50px);
  }

  > div {
    margin-top: 10px;
    margin-right: 5px;
  }
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: $blue;
  border-radius: 25px;
}

::-webkit-scrollbar-thumb:hover {
  background: $blue;
}

.containerGroupAndCountires {
  display: flex;
  flex-direction: row;

}