@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.map{
  width: 100%;
  &__container{
    background: url(./assets/mapBg.png) center center no-repeat;
    background-size: cover;
    width: calc(100% - 900px);
    position: fixed;
    right: 0;
    height: calc(100% - 64px);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: "Roboto", Arial, sans-serif;
  
    @include respond-to('medium') {
      width: calc(100% - 800px);
    }
  
    @include respond-to('small') {
      width: calc(100% - 656px);
    }
  }
  &__zoomable{
    //transition: all 150ms;
  }
  &__area{
    stroke: rgba(199, 213, 236, 0.25);
  }
}

.bubble{
  cursor: pointer;
  &__circle{
    fill: rgb(21, 24, 29);
    stroke: rgb(77, 140, 243);
  }
  &__value{
    fill: rgb(237, 241, 247);
    text-anchor: middle;
    font-weight: bold;
  }
  &__name{
    fill: rgb(237, 241, 247);
    text-anchor: middle;
    font-weight: 500;
    display: none;
  }
  &:hover{
    .bubble__name{
      display: block;
    }
  }
}

.visible{
  display: block;
}

.kn{
  position: absolute;
  right: 40px;
  top: 32px;
  display: flex;
  align-items: center;
  &__icon{
    margin-right: 16px;
  }
  &__title{
    color: #BFCBE1;
    font-size: 14px;
    margin-bottom: 2px;
  }
  &__value{
    color: #FFFFFF;
    font-size: 20px;
    font-weight: bold;
  }
}