@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.containerMenu {
  position: fixed;
  z-index: 2;
  bottom: 0;
  width: 100vw;
  height: 64px;
  display: flex;
  align-items: center;
  flex-direction: row;
  font-family: "Roboto",Arial,sans-serif;
  font-weight: 300;
  background-color: rgba(62, 81, 111, 0.25);

  @include respond-to('small') {
    height: 44px;
  }
}

.containerCounts {
  display: flex;
  align-items: flex-start;
  flex-direction: row;

  width: 179px;
  height: 32px;

  background-color: $darkest_grey;

  color: $white;
  font-family: $roboto;
  font-size: 13px;

  position: absolute;
  left: 150px;

  border-radius: 4px;

  @include respond-to('small') {
    left: 120px;

    width: 140px;
    height: 30px;

    font-size: 12px;
  }
}

.containerUnits {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;

  width: 100%;
  height: 100%;

  cursor: pointer;

  span {
    width: 100%;
    height: 100%;
    display: grid;
    justify-content: center;
    align-content: center;
    border-radius: 4px;

    &:hover {
      background-color: $grey;
    }
  }
}

.activeSpan {
  background-color: $grey;
}

.containerPeriods {
  display: flex;
  align-items: flex-end;
  flex-direction: row;

  width: 365px;
  height: 32px;

  background-color: $darkest_grey;

  color: $white;
  font-family: $roboto;
  font-size: 13px;

  position: absolute;
  right: 30px;

  border-radius: 4px;

  @include respond-to('small') {
    right: 20px;

    width: 310px;
    height: 30px;

    font-size: 12px;
  }
}

.containerSwitch {
  display: flex;
  align-items: flex-start;
  flex-direction: row;

  position: absolute;
  left: 450px;

  color: $white;
  font-family: $roboto;
  font-size: 14px;

  span:first-child {
    margin-right: 10px;
  }

  span:last-child {
    margin-left: 10px;
  }

  @include respond-to('medium') {
    left: 400px;
  }

  @include respond-to('small') {
    left: 300px;
  }
}

.active {
  color: $white;
}

.noActive {
  color: $middle_grey;
}

.switchSelect {
  outline: none;
  background-color: transparent;
  border: none;
}

.switchSelect:after {
  outline: none;
  background-color: transparent;
  border: none;
}

.icons {
  width: 29px;
  height: 29px;

  cursor: pointer;
}

.iconsMap {
  width: 25px;
  height: 25px;

  cursor: pointer;

  @include respond-to('small') {
    width: 20px;
    height: 20px;
  }
}

.containerRestoreFilters {
  display: flex;
  align-items: flex-start;
  flex-direction: row;

  position: absolute;
  left: 750px;

  color: $middle_grey;
  font-family: $roboto;
  font-size: 14px;

  cursor: pointer;

  span {
    margin-left: 10px;
  }

  @include respond-to('medium') {
    left: 580px;
  }

  @include respond-to('small') {
    left: 480px;
  }
}

.filters {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.containerMap {
  display: flex;
  align-items: flex-end;
  flex-direction: row;

  width: 82px;
  height: 40px;

  background-color: $darkest_grey;

  color: $white;
  font-family: $roboto;
  font-size: 13px;

  position: absolute;
  left: 55%;

  border-radius: 4px;

  @include respond-to('medium') {
    left: 61%;
  }

  @include respond-to('small') {
    left: 61%;

    width: 72px;
    height: 36px;
  }
}