@import
'../../../../styles/utils/variables',
'../../../../styles/utils/mixins';

.period{
  opacity: 0.75;
  cursor: pointer;
  &:hover{
    opacity: 1;
  }
  &.active{
    opacity: 1;
    .period__bar{
      background: linear-gradient(180deg, #3F71ED 46.88%, #4F91F4 100%), #3F71ED;
    }
    .period__value{
      background: #15181D;
      border: 2px solid #4F91F4;
      padding: 7px 0;
    }
  }
  &__container{
    width: 100%;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &:before{
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      width: 1px;
      height: 134px;
      background: linear-gradient(180deg, rgba(199, 213, 236, 0) 0%, rgba(109, 127, 158, 0) 0.01%, #6D7F9E 25%);
      z-index: 1;
    }
    &:after{
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 1px;
      height: 32px;
      background: #15181D;
      z-index: 1;
    }
    &:first-child{
      border-radius: 0 0 0 5px;
    }
    &:last-child{
      border-radius: 0 0 5px 0;
      &:after{
        display: none;
      }
    }
  }
  &__top{
    position: relative;
    height: 134px;
    background: linear-gradient(180deg, rgba(21, 24, 29, 0) 0%, #15181D 50.52%);
    border-bottom: 1px solid #8B9CB8;
  }
  &__bar{
    background: #3E516F;
    position: absolute;
    left: 0;
    bottom: 0;
    width: calc(100% - 1px);
    height: 0;
    border-radius: 4px 4px 0 0;
  }
  &__value{
    font-size: 16px;
    font-weight: bold;
    position: absolute;
    background: #3E516F;
    border: 1px solid rgba(199, 213, 236, 0.25);
    border-radius: 4px;
    width: 122px;
    min-height: 35px;
    left: 50%;
    margin-left: -61px;
    bottom: 100%;
    margin-bottom: -17px;
    text-align: center;
    box-sizing: border-box;
    padding: 8px 0;
    @include respond-to('medium') {
      font-size: 14px;
      width: 104px;
      margin-left: -52px;
    }
  }
  &__date{
    height: 32px;
    background: #15181D;
    text-align: center;
    font-weight: 500;
    font-size: 13px;
    box-sizing: border-box;
    padding: 9px 0;
  }
}