@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: column;

  width: 366px;
  min-height: 380px;

  overflow-y: hidden;

  padding-bottom: 25px;

  margin-right: 12px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 316px;
    min-height: 340px;
  }

  @include respond-to('small') {
    width: 244px;
    min-height: 330px;

    padding-bottom: 15px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: column;

  margin-top: 25px;
  margin-left: 20px;

  @include respond-to('medium') {
    margin-top: 15px;
    margin-left: 15px;
  }
}

.title {
  color: $light_grey;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $roboto;
  font-weight: 600;

  margin-bottom: 15px;
  margin-left: 15px;

  line-height: 1.5;

  @include respond-to('medium') {
    margin-bottom: 25px;
  }

  @include respond-to('small') {
    font-size: 13px;
    margin-bottom: 15px;
  }
}