@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: column;

  width: 366px;
  min-height: 380px;

  overflow-y: scroll;

  padding-bottom: 25px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 316px;
    min-height: 340px;
  }

  @include respond-to('small') {
    width: 244px;
    min-height: 330px;

    padding-bottom: 15px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 0;
  margin-left: 20px;

  @include respond-to('medium') {
    margin-left: 15px;
  }
}

.title {
  color: $light_grey;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $roboto;
  font-weight: 600;

  margin-top: 15px;
  margin-left: 15px;

  line-height: 1.5;

  @include respond-to('small') {
    font-size: 13px;
    margin-top: 10px;
  }
}

.containerChooseCountries {
  display: flex;
  align-items: flex-end;
  flex-direction: row;

  width: 105px;
  height: 40px;

  margin-right: 25px;
  margin-top: 20px;

  background-color: $darkest_grey;

  color: $white;
  font-family: $roboto;
  font-size: 13px;

  border-radius: 4px;

  @include respond-to('medium') {
    margin-right: 15px;
    margin-left: 10px;

    width: 210px;
    height: 35px;

    font-size: 10px;
  }

  @include respond-to('small') {
    margin-right: 0;
    margin-left: 15px;
    margin-top: 15px;

    width: 310px;
    height: 30px;

    font-size: 10px;
  }
}

.containerCountries {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;

  width: 100%;
  height: 100%;

  cursor: pointer;

  span {
    width: 100%;
    height: 100%;
    display: grid;
    justify-content: center;
    align-content: center;
    border-radius: 4px;

    &:hover {
      background-color: $grey;
    }
  }
}

.activeSpan {
  background-color: $grey;
}

.containerProgressBarLines {
  display: flex;
  flex-direction: column;

  margin-top: 20px;
  margin-left: 34px;
  width: 84%;

  @include respond-to('medium') {
    margin-left: 30px;
    margin-top: 15px;
  }
}

.containerProgressBarLinesDetailed {
  display: flex;
  flex-direction: column;

  margin-top: 20px;

  @include respond-to('medium') {
    margin-left: 30px;
    margin-top: 15px;
  }
}

.containerProgressIcon {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.containerProgressTitle {
  display: flex;
  flex-direction: column;
}

.progressBarTitle {
  color: $light_grey;
  font-family: $roboto;
  font-size: 14px;

  @include respond-to('small') {
    font-size: 12px;
  }
}

.progressBarCost {
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;

  color: $white;
  font-family: $roboto;
}

.iconDropdown {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  margin-right: 0;
  margin-top: 10px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-top: 10px;
  }
}

.progressLine {
  position: absolute;
  margin-top: 50px;

  height: 1%;
  width: 40%;

  @include respond-to('medium') {
    width: 45%;
    height: 1.5%;
  }

  @include respond-to('small') {
    width: 44%;
  }
}

.iconCountry {
  border-radius: 50%;
  object-fit: cover;
  margin-top: 10px;
  width: 30px;
  height: 30px;
}

.iconDropdown {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  margin-right: 0;
  margin-top: 10px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-top: 10px;
  }
}