@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: row;

  width: 744px;
  min-height: 272px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 644px;
    min-height: 220px;
  }

  @include respond-to('small') {
    width: 500px;
    min-height: 210px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: row;

  margin-top: 44px;
  margin-left: 32px;

  @include respond-to('medium') {
    margin-top: 15px;
    margin-left: 15px;
  }
}

.containerProgressBar {
  display: flex;
  flex-direction: column;

  width: 100%;
  margin-left: 32px;

  @include respond-to('medium') {
    margin-left: 22px;
  }
}

.containerProgressBarLines {
  display: flex;
  flex-direction: column;

  margin-top: 30px;

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.progressLine {
  position: absolute;
  margin-top: 50px;

  width: 51.5%;

  @include respond-to('small') {
    width: 46%;
  }
}

.icons {
  width: 72px;
  height: 72px;

  cursor: pointer;

  @include respond-to('small') {
    width: 62px;
    height: 62px;
  }
}

.iconDropdown {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  margin-right: 35px;
  margin-top: 10px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-right: 15px;
    margin-top: 10px;
  }

  @include respond-to('small') {
    margin-right: 15px;
  }
}

.containerProgressIcon {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.containerProgressTitle {
  display: flex;
  flex-direction: column;
}

.title {
  color: $white;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $roboto;
  font-weight: 600;

  margin-top: 2px;
  margin-left: 15px;

  line-height: 1.5;

  @include respond-to('small') {
    font-size: 13px;
  }
}

.containerTotalCost {
  display: flex;
  flex-direction: column;

  margin-top: 35px;
  margin-left: 32px;

  @include respond-to('medium') {
    margin-top: 30px;
  }
}

.titleTotalCost {
  color: $white;
  text-transform: uppercase;
  font-size: 44px;
  font-family: $roboto;
  font-weight: 600;

  @include respond-to('medium') {
    font-size: 40px;
  }

  @include respond-to('small') {
    font-size: 35px;
  }
}

.titleUnit {
  color: $white;

  font-size: 25px;
  font-family: $roboto;
  font-weight: 600;

  margin-left: 42px;
  margin-top: -10px;

  @include respond-to('medium') {
    font-size: 23px;
    margin-left: 35px;
  }

  @include respond-to('small') {
    margin-left: 30px;
  }
}

.progressBarTitle {
  color: $light_grey;
  font-family: $roboto;
  font-size: 14px;

  @include respond-to('small') {
    font-size: 12px;
  }
}

.progressBarCost {
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;

  color: $white;
  font-family: $roboto;
}