@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: row;

  width: 744px;
  min-height: 272px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 644px;
    min-height: 220px;
  }

  @include respond-to('small') {
    width: 500px;
    min-height: 210px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: row;

  margin-top: 44px;
  margin-left: 32px;

  @include respond-to('medium') {
    margin-top: 15px;
    margin-left: 15px;
  }
}

.containerProgressBar {
  display: flex;
  flex-direction: column;

  width: 51%;
  margin-top: 35px;
  margin-left: 32px;

  font-family: $roboto;

  svg {
    border-radius: 8px;
  }

  @include respond-to('medium') {
    margin-left: 22px;
    margin-top: 15px;

    width: 48%;
  }

  @include respond-to('small') {
    width: 46%;

    margin-left: 15px;
  }
}

.icons {
  width: 74px;
  height: 74px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 72px;
    height: 72px;
  }

  @include respond-to('small') {
    width: 62px;
    height: 62px;
  }
}

.title {
  color: $white;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $roboto;
  font-weight: 600;

  margin-top: 15px;
  margin-left: 15px;

  line-height: 1.5;

  @include respond-to('small') {
    font-size: 13px;
    margin-top: 10px;
  }
}

.containerTotalCost {
  display: flex;
  flex-direction: column;

  margin-top: 35px;
  margin-left: 32px;

  @include respond-to('medium') {
    margin-top: 30px;
  }
}

.titleTotalCost {
  color: $white;
  text-transform: uppercase;
  font-size: 44px;
  font-family: $roboto;
  font-weight: 600;

  @include respond-to('medium') {
    font-size: 40px;
  }

  @include respond-to('small') {
    font-size: 35px;
  }
}

.titleUnit {
  color: $white;

  font-size: 25px;
  font-family: $roboto;
  font-weight: 600;

  margin-left: 42px;
  margin-top: -10px;

  @include respond-to('medium') {
    font-size: 23px;
    margin-left: 35px;
  }

  @include respond-to('small') {
    margin-left: 30px;
  }
}

.containerlegend {
  display: flex;
  flex-direction: column;

  .legend:not(:last-child) {
    border-bottom: 1px solid #8B9CB8;
  }
}

.legend {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  font-family: $roboto;

  line-height: 3;

  @include respond-to('medium') {
    line-height: 2.8;
  }
}

.legendName {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  font-size: 17px;

  color: $light_grey;

  @include respond-to('medium') {
    font-size: 16px;
  }

  @include respond-to('small') {
    font-size: 12px;
  }
}

.legendDescription {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  font-size: 17px;

  color: $total_white;
  font-weight: 600;

  @include respond-to('medium') {
    font-size: 16px;
  }

  @include respond-to('small') {
    font-size: 12px;
  }
}

.circle {
  margin-top: 18px;
  margin-right: 10px;

  box-shadow: 0px 0px 4px #000000;

  @include respond-to('medium') {
    margin-top: 17px;
  }

  @include respond-to('small') {
    margin-top: 11px;
  }
}

.ellipse {
  margin-top: 14px;
  margin-left: -24px;

  padding-right: 15px;

  @include respond-to('medium') {
    margin-top: 13.2px;
    margin-left: -23.9px;
  }

  @include respond-to('small') {
    margin-top: 7px;
    margin-left: -24px;
  }
}

.containerIcons {
  display: flex;
  flex-direction: row;
}