@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: column;

  width: 744px;
  min-height: 240px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 644px;
    min-height: 210px;
  }

  @include respond-to('small') {
    width: 500px;
    min-height: 200px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  margin-top: 20px;
  margin-left: 20px;

  width: 92%;

  @include respond-to('medium') {
    width: 94.5%;
  }

  @include respond-to('small') {
    width: 93%;
  }
}

.title {
  font-family: $roboto;
  color: $light_grey;
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;

  margin-top: 4px;
  margin-left: 10px;

  @include respond-to('small') {
    font-size: 13px;
  }
}

.count {
  font-family: $roboto;
  color: $white;
  font-size: 25px;
  font-weight: 600;

  justify-content: flex-end;

  @include respond-to('small') {
    font-size: 20px;
  }
}

.containerBack {
  display: flex;
  flex-direction: row;
}

.containerProgressBarLines {
  display: flex;
  flex-direction: column;

  margin-top: 25px;
  margin-left: 20px;

  @include respond-to('medium') {
    margin-top: 15px;
  }
}

.progressBarTitle {
  color: $light_grey;
  font-family: $roboto;
  font-size: 14px;

  @include respond-to('small') {
    font-size: 12px;
  }
}

.progressBarCost {
  font-weight: bold;
  font-size: 18px;
  line-height: 34px;

  color: $white;
  font-family: $roboto;
}

.progressLine {
  position: absolute;
  margin-top: 50px;

  width: 44%;

  @include respond-to('medium') {
    width: 45%;
    height: 1.5%;
  }

  @include respond-to('small') {
    width: 44%;
  }
}

.detailedInfo {
  display: flex;
  flex-direction: column;
}

.containerDetailedInfo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  width: 68%;

  @include respond-to('medium') {
    width: 72%;
  }

  @include respond-to('small') {
    width: 74.5%;
  }
}

.icons {
  cursor: pointer;
}