@import
'../../styles/utils/variables',
'../../styles/utils/mixins';

.container {
  display: flex;
  flex-direction: row;

  width: 744px;
  min-height: 190px;

  background-color: rgba(62, 81, 111, 0.25);
  border-radius: 8px;

  @include respond-to('medium') {
    width: 644px;
    min-height: 150px;
  }

  @include respond-to('small') {
    width: 500px;
    min-height: 110px;
  }
}

.title {
  color: $light_grey;

  font-family: $roboto;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 600;

  @include respond-to('small') {
    font-size: 12px;
  }
}

.containerTitle {
  display: flex;
  flex-direction: column;

  margin-top: 66px;
  margin-left: 32px;

  width: 185px;

  @include respond-to('medium') {
    width: 180px;
  }

  @include respond-to('small') {
    margin-top: 50px;
    width: 135px;
  }
}

.containerChart {
  display: flex;
  flex-direction: row;

  width: 30%;

  @include respond-to('medium') {
    width: 25%;
  }
}

.containerLegend {
  display: flex;
  flex-direction: column;

  width: 36.5%;

  margin-top: 25px;

  .legend:not(:last-child) {
    border-bottom: 1px solid #8B9CB8;
  }

  @include respond-to('medium') {
    width: 39.5%;
  }

  @include respond-to('small') {
    width: 38.5%;
  }
}

.legend {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  font-family: $roboto;

  line-height: 3;

  @include respond-to('medium') {
    line-height: 2.8;
  }
}

.legendName {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;

  font-size: 17px;

  color: $light_grey;

  @include respond-to('medium') {
    font-size: 16px;
  }

  @include respond-to('small') {
    font-size: 12px;
  }
}

.legendDescription {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

  font-size: 17px;

  color: $total_white;
  font-weight: 600;

  @include respond-to('medium') {
    font-size: 16px;
  }

  @include respond-to('small') {
    font-size: 12px;
  }
}

.circle {
  margin-top: 18px;
  margin-right: 10px;

  box-shadow: 0px 0px 4px #000000;

  @include respond-to('medium') {
    margin-top: 17px;
  }

  @include respond-to('small') {
    margin-top: 11px;
  }
}

.ellipse {
  margin-top: 14px;
  margin-left: -24px;

  padding-right: 15px;

  @include respond-to('medium') {
    margin-top: 13.2px;
    margin-left: -23.9px;
  }

  @include respond-to('small') {
    margin-top: 7px;
    margin-left: -24px;
  }
}

.containerIcons {
  display: flex;
  flex-direction: row;
}

.show {
  display: flex;
  flex-direction: row;
}

.iconDropdown {
  margin-top: 12px;
  margin-left: 15px;

  cursor: pointer;

  @include respond-to('medium') {
    margin-top: 10px;
  }

  @include respond-to('small') {
    margin-top: 4px;
    width: 20px;
  }
}