.breadcrumb{
  position: absolute;
  left: 16px;
  top: 40px;
  color: #EDF1F7;
  font-size: 14px;
  &>ul{
    list-style: none;
    &>li{
      display: inline-block;
      margin-right: 13px;
    }
  }
  &__link{
    color: #3F71ED;
    cursor: pointer;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
  &__title{
    font-size: 24px;
    font-weight: 500;
  }
  &__arrow{
    position: relative;
    margin-right: 13px;
    top: 2px;
  }
}