@mixin respond-to($media) {
  @if $media == small {
    @media only screen and (max-width: 1270px) { @content; }
  }

  @else if $media == medium {
    @media only screen and (max-width: 1645px) { @content; }
  }

  @else if $media == large {
    @media only screen and (max-width: 1920px) { @content; }
  }
}
